body {
  --header-text-color: #00acdc;
  margin: 0;
  font-family: Roboto, Helvetica, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  display: block;
}

.nav {
  font-size: 12px;
  font-weight: 500;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-of-type) {
  margin-left: 20px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--header-text-color);
}

.is-active:after {
  content: "";
  background-color: var(--header-text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
